.sidebarrow{
    display: flex;
    align-items: center;
    padding: 10px 20px;
}
.sidebarrow_icon {
    color : #606060;
    font-size: 30px !important;

}
.sidebarrow_title{
    flex: 1;
    margin-left: 20px;
    font-size: 22px;
    font-weight: 600;

}
.sidebarrow:hover{
    background-color: lightgray;
    cursor: pointer;
}
.sidebarrow:hover > .sidebarrow_icon {
    color: red;
}
.sidebarrow:hover > .sidebarrow_title{
    font-weight: bold;
}
.sidebarrow.selected > .sidebarrow_icon{
    color: red;
}
.sidebarrow.selected > .sidebarrow_title{
    font-weight: bold;
}